
#skills{
    margin-top: 5em;
}

#skills .img{
    width: 600px;
    position: relative;
    top: -250px;
}

.skills-content{
    text-align: center;
    /* padding-right: 90px; */
    width: 100%;
}

.text-title-1{
    font-style: italic;
    padding-left: 20rem;
}
.text-title-1 span{
    color: var(--color-red);
    font-weight:800 ;
}
#skills .icon{
    font-size: 1.5em;
    margin-left: 30%;

    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 0.5rem;
}
.icon i{
    /* color: blue; */
    color: #C0C0C0;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0.5rem;
} 
.icon i span{
    /* display: block; */
    margin-left: 0.5rem;
}
.icon i:hover{
    color: var(--color-red);
    transform: scale(1.3);
    transition: all 0.3s ease-in-out;
    -webkit-transform: scale(1.3);
    -moz-transform: scale(1.3);
    -ms-transform: scale(1.3);
    -o-transform: scale(1.3);
}

@media(max-width : 768px){
    #skills .img{
        /* display: none; */
        position: relative;
        top: 20px;
        left: 100px;
    }
    #skills .icon{
        justify-content:center;
        position: relative;
        right: 30px;
        margin-right: 10%;
    }
    #skills .icon i{
        white-space: normal;
        /* margin-right: 80px; */
    }
    .text-title-1{
        padding-left: 0;
    }
}
